.layout > nav ul {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
  min-height: 3rem;
}
.layout > nav li {
  height: 3rem;
  width: 100%;
}
.layout > nav li.logo {
  z-index: 1;
  flex: 1;
  margin: 0 0 0.8em 0;
}
.layout > nav li.navlink {
  position: relative;
  flex: 1;
}
.layout > nav li a {
  position: relative;
  display: inline-block;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  color: #221111;
  font-size: 1.3rem;
  height: 100%;
  width: 100%;
  vertical-align: middle;
  line-height: 3rem;
}
.layout > nav li.logo a {
  white-space: nowrap;
  font-size: 4.8rem;
  margin-top: -0.4rem;
}
.layout > nav li.logo a:before {
  content: "{";
  padding-right: 1rem;
}
.layout > nav li.logo a:after {
  content: "}";
  padding-left: 1rem;
}
.layout > nav li.logo a:before, .layout > nav li.logo a:after {
  font-size: 2rem;
  transform: translateY(-12px);
  display: inline-block;
  color: #AA7777;
}
.layout > nav li.current a {
  color: #FFFFFF;
  background: #3D3333;
  text-decoration: underline;
}
@media (hover: hover) {
  .layout > nav li:not(.current).navlink:hover a {
    color: #221111;
  }
  .layout > nav li:not(.current).navlink:hover a::after {
    content: "";
    position: absolute;
    width: calc(100% - 0.5rem);
    height: calc(100% - 0.5rem);
    top: 0.25rem;
    right: 0.25rem;
    bottom: 0.25rem;
    left: 0.25rem;
    background-color: #3D3333;
    opacity: 0.2;
    border-radius: 0.2rem;
  }
  .layout > nav li.navlink:active {
    background: #221111;
  }
  .layout > nav li:not(.current).navlink:active a {
    color: #FFFFFF;
  }
}
@media only screen and (min-width: 800px) {
  .layout > nav ul {
    flex-direction: row;
  }
  .layout > nav li.logo {
    margin: 0;
  }
  .layout > nav li.logo a {
    width: min-content;
    padding: 0 1rem;
  }
  .layout > nav li.navlink {
    flex: 0 1 140px;
  }
  .layout > nav li:not(.current).navlink:not(:last-child):after {
    content: "";
    opacity: 0.2;
    position: absolute;
    height: 60%;
    border-left: 2px solid #3D3333;
    width: 1px;
    top: 20%;
    right: -2px;
  }
}

.layout > main {
  margin: 0 1rem 2rem;
}