.image_loader {
  position: relative;
  background-color: #221111;
}
.image_loader > svg > rect {
  fill: #221111;
}
.image_loader > img {
  position: relative;
  width: 100%;
}
.image_loader.hidden > img {
  display: none;
}
.image_loader:not(.hidden) > svg {
  position: absolute;
}
.image_loader:not(.hidden) > img {
  animation: fadeIn ease-in 0.4s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}